@import "../components/variables";
@import "../components/mixin";

body{
  .certificates{
    margin: 25px 0 100px;
    width: 100%;
    .containerParent();
    .container{
      .containerChild();
      display: grid;
      grid-template-areas: "breadcrumb"
                           "certificates";
      grid-template-columns: 100%;
      .content__certificate__breadcrumb{
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__certificates{
        grid-area: certificates;
        &__headline{
          display: flex;
          justify-content: space-between;
          align-items: center;
          h5 {
            grid-area: headline;
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
          }
          a{
            display: flex;
            justify-content: center;
            width: 250px;
            height: 50px;
            font: 1em/50px @sansbold;
            color: #fff;
            text-transform: uppercase;
            text-decoration: none;
            background: @mainColor;
            border-radius: 5px;
            i{
              font-size: 1.5em;
              line-height: 50px;
            }
            &:hover{
              background-color: @hoverColor;
              transition: .5s;
            }
          }
        }
        .firstBlock,
        .secondBlock{
          h6{
            font: 1.125em/1.25em @sansbold;
            color: @linkColor;
          }
          ul{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            li{
              margin: 12.5px;
              width: 17%;
              text-align: center;
              img{
                width: 100%;
                &:hover{
                  cursor: pointer;
                }
              }
            }
            .large{
              display: none;
            }
          }
        }
        .firstBlock{
          h6{
            margin: 15px 0;
          }
        }
        .secondBlock{
          h6{
            margin: 45px 0 25px;
          }
        }
      }
    }
  }
}