.containerParent {
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
.containerChild {
  grid-area: container;
}
body .certificates {
  margin: 25px 0 100px;
  width: 100%;
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
body .certificates .container {
  grid-area: container;
  display: grid;
  grid-template-areas: "breadcrumb" "certificates";
  grid-template-columns: 100%;
}
body .certificates .container .content__certificate__breadcrumb {
  grid-area: breadcrumb;
}
body .certificates .container .content__certificate__breadcrumb ul {
  display: flex;
}
body .certificates .container .content__certificate__breadcrumb ul li a {
  font: 0.875em/1em "PTSans-Regular";
  color: #474747;
}
body .certificates .container .content__certificate__breadcrumb ul li span {
  font: 0.875em/1em "PTSans-Regular";
  color: #474747;
}
body .certificates .container .content__certificates {
  grid-area: certificates;
}
body .certificates .container .content__certificates__headline {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
body .certificates .container .content__certificates__headline h5 {
  grid-area: headline;
  font: 2.25em/2.375em "PTSerif-Italic";
  color: #0e3a40;
}
body .certificates .container .content__certificates__headline a {
  display: flex;
  justify-content: center;
  width: 250px;
  height: 50px;
  font: 1em/50px "PTSans-Bold";
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  background: #00929e;
  border-radius: 5px;
}
body .certificates .container .content__certificates__headline a i {
  font-size: 1.5em;
  line-height: 50px;
}
body .certificates .container .content__certificates__headline a:hover {
  background-color: #9e79ac;
  transition: .5s;
}
body .certificates .container .content__certificates .firstBlock h6,
body .certificates .container .content__certificates .secondBlock h6 {
  font: 1.125em/1.25em "PTSans-Bold";
  color: #0e3a40;
}
body .certificates .container .content__certificates .firstBlock ul,
body .certificates .container .content__certificates .secondBlock ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
body .certificates .container .content__certificates .firstBlock ul li,
body .certificates .container .content__certificates .secondBlock ul li {
  margin: 12.5px;
  width: 17%;
  text-align: center;
}
body .certificates .container .content__certificates .firstBlock ul li img,
body .certificates .container .content__certificates .secondBlock ul li img {
  width: 100%;
}
body .certificates .container .content__certificates .firstBlock ul li img:hover,
body .certificates .container .content__certificates .secondBlock ul li img:hover {
  cursor: pointer;
}
body .certificates .container .content__certificates .firstBlock ul .large,
body .certificates .container .content__certificates .secondBlock ul .large {
  display: none;
}
body .certificates .container .content__certificates .firstBlock h6 {
  margin: 15px 0;
}
body .certificates .container .content__certificates .secondBlock h6 {
  margin: 45px 0 25px;
}
